.App {
  font-family: Arial, sans-serif;
  text-align: center;
  margin: 20px;
}

h1 {
  font-size: 28px;
  color: #333;
}

.product-form {
  border: 1px solid #ccc;
  padding: 20px;
  margin: 10px auto;
  max-width: 300px;
  background-color: #f9f9f9;
  border-radius: 4px;
}

.product-form h2 {
  font-size: 18px;
  margin: 0;
  color: #333;
}

.form-input {
  display: flex;
  align-items: center;
  margin: 10px 0;
}

.form-input label {
  flex: 1;
  margin-right: 10px;
}

.form-input input {
  flex: 3;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.product-form button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 4px;
  margin-top: 10px;
}

.product-form button:hover {
  background-color: #0056b3;
}

.product-list {
  margin-top: 20px;
  max-width: 400px;
  margin: 20px auto;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #f9f9f9;
  padding: 20px;
}

.product-list h2 {
  font-size: 18px;
  margin: 0;
  color: #333;
}

.product-list ul {
  list-style: none;
  padding: 0;
}

.product-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
}

.product-list button {
  background-color: #ff3b3b;
  color: #fff;
  border: none;
  padding: 6px 12px;
  cursor: pointer;
  border-radius: 4px;
}

.product-list button:hover {
  background-color: #e63737;
}

.total-value {
  margin-top: 20px;
  font-size: 16px;
  color: #333;
}